.HomeTitleText {
    color: aliceblue;
    height: 100vh;
    z-index: 1;
    display: flex;
    text-align: center;
    justify-content: center; /* Vertically centers the content */
    align-items: center;
    flex-direction: column; /* Aligns children in a column */
}
.HomeTitleText h1 {
    color: aliceblue;
    margin: -20vh 0 0 0; /* 向上偏移20px，调整这个值来满足你的需求 */
    font-size:300%;
    
}
.HomeTitleText p {
    font-size: x-large;
}


  nav {
    z-index: 100;
    position: relative;
  }

.box{
    /* font-family:'Cormorant Infant'; */
    font-size:x-large;
    display: flex;
    flex-direction: row;
    margin: auto;
    margin-top: 3%;
    align-items: center; 
    justify-content: center;

}
.box h1{
    font-size: 240%;
    text-align: left;
}
.box h2{
    /* font-size: 240%; */
    text-align: left;
}
.imagebox{
    
    flex: 1;
}
.textbox {
    flex: 1;
    margin: 0 10%;
    text-align: left;
}

.textbox button {
    background-color: #007BFF; /* 按钮背景颜色 */
    color: white; /* 按钮文字颜色 */
    border: none; /* 无边框 */
    border-radius: 5px; /* 圆角 */
    padding: 10px 20px; /* 内边距 */
    font-size: 16px; /* 字体大小 */
    cursor: pointer; /* 鼠标悬停时为手型 */
    transition: background-color 0.3s ease; /* 背景颜色过渡效果 */
    margin-right: 3vh;
}
.textbox button:hover {
    background-color: #0056b3; /* 悬停时的背景颜色 */
}

/* 轮播图 */

.carousel {
    width: 100%;
    /* max-width: 1000px; */
    height: auto;
    margin: auto;
    /* padding: 20px; */
    overflow: hidden;
    position: relative; 
}

.slideshow-container {
    position: relative;
    width: 100%;
    height: 400px;
    overflow: hidden;
}

.slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 1s ease;
}

.slide.active {
    opacity: 1;
}

.slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
